import React from 'react';
import '../../../styles/flow.css';
import { Modal , Button} from 'react-bootstrap';
import FileUpload from '../../common/component/FileUpload';
import FlowComponent from '../../common/component/core/FlowComponent';
import {loadAgreementDetails, saveAgreement} from '../../../actions/agreement_actions';
import SignatureCanvas from 'react-signature-canvas';
import { Redirect } from 'react-router-dom';

class UploadAgreementV2 extends FlowComponent {

	state = {
		viewPdf : false,
		sign_checked: false,
		upload_checked: false,
		sign_data: "",
		toapplyFA: false,
		new_agree_signin : false,
		witness : {}
	}
	componentWillMount(){
		
		const app_url = process.env.REACT_APP_URL;
		if(process.env.NODE_ENV !== 'development'){
            //this.root = "http://docs.google.com/viewer?embedded=true&url=" + app_url;
            this.root = ""; 
        }else{
            this.root = "http://localhost:8000"; 
        }
        
        var draft_aggr_doc_id = null;
		this.file_format = 'image/png'; 
		
		this.cust_id = this.props.match.params.cust_id;
		
		loadAgreementDetails(this.req({"cust_id": this.cust_id}))
        .then((response) => {
        this.resp(response);
          if(!response){return };
            if(response.status === "success"){                  
            	
				const file_path = response.data.master_aggr_folder_rel_path
				
				 const aggr_doc_id = response.data.master_agreement.aggr_doc_id;
				 
				 var aggr_file_rel_path  = file_path + "/" + aggr_doc_id + ".pdf";
				 var cust_name = response.data.cust_name;
				 var rm_name = response.data.rm_name;
				 
             	this.setState({master_agreement : response.data.master_agreement, aggr_file_rel_path, cust_name, rm_name});
            }else{
              alert(response.message + "\n" + JSON.stringify(response.data));  
            }
          }
        );
    	

	}

    handleClose = (event) => {
    	this.setState({modalBox: false, witness : {}});
  	}

    handleSignAgreement = (event) => {
		
    	this.setState({modalBox: true});
    }

	handleOnChange = (event) =>{

		var witness = this.state.witness
		witness[event.target.id] = event.target.value
		this.setState({witness});
		
	
	}
	handleClearSignature =() =>{
		this.signature_cust.clear();
		this.signature_rm.clear();
		this.signature_witness.clear();
		this.setState({witness : {}});
		
	}
    handleCaptureSignature = (event) => {
    	var resp = true;
		// event.target.value = this.signature.getTrimmedCanvas().toDataURL(this.file_format); 
		var cust_sign = this.signature_cust.getTrimmedCanvas().toDataURL(this.file_format); 
		var rm_sign = this.signature_rm.getTrimmedCanvas().toDataURL(this.file_format); 
		var witness_sign = this.signature_witness.getTrimmedCanvas().toDataURL(this.file_format); 
		console.log(this.state.witness.witness_name);
		
    	this.handleSelfChangeState(event);    	
    	
		
		if(this.state.witness.witness_name == null){
			alert("Please enter witness name.");
			return;
		}

		if(this.signature_cust.isEmpty()){
			alert("Please capture customer's signature digitally to submit.");
			return;
		}
		if(this.signature_rm.isEmpty()){
			alert("Please capture RM's signature digitally to submit.");
			return;
		}
		if(this.signature_witness.isEmpty()){
			alert("Please capture witness' signature digitally to submit.");
			return;
		}
		this.setState({modalBox: false});
    	resp = window.confirm("Are you sure to sign and submit the agreement.")
    	if(resp == true){
    		
    		
	  		const file_of = "agreement_signature";
	  		
			const request = {"cust_id": this.cust_id, "aggr_type": this.state.aggr_type, 
							 "aggr_doc_id": this.state.master_agreement.aggr_doc_id}

	  		const file_req = {"country_code": this.country_code,file_of,
	  		                 "file_name" :  this.state.master_agreement.aggr_doc_id,
							 
							 "file_type" : this.file_format,
							 "file_data_type" : "data-url"
	        				};
	   
			
  			
			const rm_sign_req = {...file_req, file_data : rm_sign, sign_of : "rm"}
			const cust_sign_req = {...file_req, file_data : cust_sign, sign_of : "cust"}
			const witness_sign_req = {...file_req, file_data : witness_sign, sign_of : "witness"}

			request.witness_name = this.state.witness.witness_name
			  
	        
	    	saveAgreement(this.req({...request, rm_sign_req, cust_sign_req, witness_sign_req}, true))
	    	.then((response) => {
	    		this.resp(response);
	          if(!response){return };
	            if(response.status === "success"){  
	              var split_cust_id = this.cust_id.split('-');
	              
					//this.setState({new_agree_signin :true});
	              	alert("Signed Agreement saved successfully -"+ this.state.master_agreement.aggr_doc_id +"-"+split_cust_id[1]);
	             
	              this.setState({new_agree_signin: true});
	              //window.location.reload();
	            }else{
	              alert(response.message + "\n" + JSON.stringify(response.data));  
	            }
	          }
	        );
    	}else{
    		return;
    	}
    	
    }

	render(){

		if(this.state.viewSignedAggr){
			return(
				<>To View Signed Agreeemnt here</>
				);
		}
		if(this.state.new_agree_signin){
			return(
				<Redirect to={`/agreement/list/${this.cust_id}`} />
				);
		}
		return(
		<div className="container">
			<div className="row">
				<h3>{this.cust_id} - Sign / Upload Agreement</h3>
			</div>
			<br/>
				{this.state.loading &&
					<span>Please wait while Agreement PDF is loaded.</span>
				}
				
					<>
					<div className="row" id="float_style">
						<input type="button" disabled={this.state.calling_api} value="Sign Agreement" onClick={this.handleSignAgreement} className="btn btn-primary" />
					</div>
					<div className="row">


						<h4>Master Agreement Document</h4>&nbsp;&nbsp;&nbsp;
						{this.note &&
							<span className="cust_details">Note : Customer details will be automatically filled once document is digitally signed </span>
						}
							{/*<object type="application/pdf" className="aggr_style" data={this.root + this.state.aggr_file_rel_path}></object>*/}
							<iframe className="aggr_style" src={this.root + this.state.aggr_file_rel_path} ></iframe>
							{/*<object className="aggr_style" data={this.root + this.state.aggr_file_rel_path} alt={this.state.aggr_file_rel_path}></object>*/}
							{/*<object className="aggr_style" data={this.root + this.state.aggr_file_rel_path} alt={this.state.aggr_file_rel_path}></object>*/}
				
					</div>
					<br/>
					<div className="row" id="float_style">
						<input type="button" disabled={this.state.calling_api} value="Sign Agreement" onClick={this.handleSignAgreement} className="btn btn-primary" />
					</div>
					</>
			<br/>
			

			<Modal show={this.state.modalBox} onHide={this.handleClose} dialogClassName="modal-dialog-sign" >
			        <Modal.Header closeButton show={this.state.modalBox} onHide={this.handleClose}>
			            <Modal.Title>Sign here</Modal.Title>
			        </Modal.Header>

			        <Modal.Body>
					<div className = "d-flex justify-content-around"> 
						<div>
							<SignatureCanvas penColor="blue" canvasProps={{width:250, height:120,className: 'signature_style'}} 
										ref={(ref) => {this.signature_cust = ref}} />
							<div><span><b>{this.state.cust_name} (Customer)</b></span></div>
							
						</div>
						<div>
							<SignatureCanvas penColor="blue" canvasProps={{width:250, height:120,className: 'signature_style'}} 
										ref={(ref) => {this.signature_rm = ref}} />
							<div><span><b>{this.state.rm_name} (Flow RM)</b></span></div>
							
						</div>	
						<div>
							<SignatureCanvas penColor="blue" canvasProps={{width:250, height:120,className: 'signature_style'}} 
										ref={(ref) => {this.signature_witness = ref}} />
							<div><span><b>Enter Witness Name : </b></span>
							<input type='text' id = 'witness_name' onChange={(e) =>{this.handleOnChange(e);}}/> </div>
							
						</div>
					</div>
				             

			        </Modal.Body>

			        <Modal.Footer>
			        <div className="center">
			        	<Button  variant="primary" id="sign_data" onClick={this.handleCaptureSignature}>Capture Signature</Button>
			        	</div>
			        	<div className="center_btn">
			        	<Button variant="secondary" onClick={this.handleClearSignature}>Clear</Button>
			        	</div>
			        	
				        <Button variant="secondary" onClick={this.handleClose}>Close</Button>
			        </Modal.Footer>
			    </Modal>

		</div>
	
			);
	}
}

export default UploadAgreementV2;
